<script>
// 柱状图
let Echarts = require("echarts/lib/echarts"); //基础实例 注意不要使用import
require("echarts/lib/chart/line"); //按需引入 bar = 柱状图
export default {
  data() {
    return { 
      //图表
            chart1: null,
      // 表格1
            tableData: [{
            proposals: '3,128',
            proposalscreators: '2,213',
            successful: '16.3%',
            participation:'94.3'
          }],
         // 表格2
            tableData2: [{
            totalvotes: '271,123',
            voters: '76,198',
            avg: '86'
          }],
         };
  }, //图表实例
  mounted() {
    this.init();
  },
  methods: {
    init() {
      //2.初始化
      this.chart = Echarts.init(this.$refs.chart);
      //3.配置数据
      let option = {
  xAxis: {
    name:'日期',
    type: 'category',
    data: ['2021-05','2021-06', '2021-07', '2021-08', '2021-09', '2021-10', '2021-11', '2021-12', '2022-01', '2022-02', '2022-03', '2022-04']
  },
  yAxis: {
    name:'投票',
    type: 'value'
  },
  series: [
    {
      data: [4195,4522, 9215, 17208, 5528, 16864, 107175, 337714,0,0,0,0],
      type: 'bar',
      itemStyle: {
							normal: {
								label: {
									show: true, //开启显示
									position: 'top', //在上方显示
									textStyle: { //数值样式
										color: 'black',
										fontSize: 16
									}
								}
							}
						},
    }
  ]
};
      // 4.传入数据
      this.chart.setOption(option);
    },
  },
};




</script>





